<template>
  <div>
    <div v-if="canCreate">
      <form @submit.prevent="createEvent()" action="" method="post">

        <div class="mb-3">
          <label for="" class="form-label">Title</label>
          <input type="text" class="form-control" v-model="event.title" name="" id="" aria-describedby="helpId" placeholder="">
          <small id="helpId" class="form-text text-muted">Help text</small>
        </div>

        <div class="mb-3">
          <label for="" class="form-label">Description</label>
          <textarea class="form-control" v-model="event.description" name="" id="" rows="3"></textarea>
        </div>

        <label for="due_date">Due date (date and time):</label>
        <input type="datetime-local" v-model="event.dueDate" id="" name="">

        <div class="form-check">
          <input type="checkbox" class="form-check-input" v-model="event.isPrivate" value="checkedValue" checked>
          <label class="form-check-label" for="">
            Private
          </label>
        </div>

        <div class="mb-3">
          <label for="" class="form-label">Category</label>
          <select class="form-control" v-model="event.category">
            <option v-for="cat in categories" :key="cat.id" :value="cat.id">{{ cat.title }}</option>
          </select>
        </div>
        
        <br>
        <br>
        <button type="submit" class="btn btn-primary">Submit</button>
      </form>
    </div>
    {{ event }}
    <br>
    Errors:
    <br>
    {{ createErrors }}
  </div>
</template>

<script>
import EventService from '@/services/event.service.js';

export default {
  data() {
    return {
      canCreate: false,
      categories: [],
      createErrors: [],
      event: {
        title: '',
        description: '',
        dueDate: '',
        isPrivate: true,
        category: null
      }
    }
  },
  created() {
    EventService.createPrepare()
      .then(res=> {
        let data = res.data.data;
        this.canCreate = true;
        this.categories = data.categories;
      })
      .catch(err=> console.log(err.response))
  },
  methods: {
    createEvent() {
      EventService.create(this.event)
        .then(res => {
          this.$router.push({name: 'showEvent', params:{id: res.data.id}});
        })
        .catch(err=> this.createErrors = err.response.data.errors)
    }
  }
}
</script>